import Component from "vue-class-component";
import mixin from "../../config/mixin";
import { Action } from "vuex-class";
import { Toast } from "vant";
import comhead from "../../components/Comhead.vue";

@Component({
  components: {
    comhead
  },
  mixins: [mixin]
})
export default class coupon extends Vue {
  tablist = ["未使用", "已使用", "已失效"];
  go_collar_center() {
    this.$router.push("/collar_center");
  }
  handlePX(CssName, PxNumber) {
    return (
      CssName +
      ":" +
      this.$store.getters[Vue.prototype.MutationTreeType.SYSTEM].availWidth /
        750 *
        PxNumber +
      "px;"
    );
  }
  couponList = [];
  active = 0;

  loading = false;
  loadMore() {
    console.log("刷新");
    this.loading = true;
    let self = this;
    setTimeout(() => {
      if (!self.loading) {
        switch (self.active) {
          case 0:
            self.unusedindexPage += 1;
            break;
          case 1:
            self.usedindexPage += 1;
            break;
          case 2:
            self.overdueindexPage += 1;
            break;
        }
        self.getList();
        self.loading = false;
      }
    }, 1000);
  }

  unusedindexPage = 0;
  usedindexPage = 0;  
  overdueindexPage = 0;

  changeTab(active) {
    this.active = active;
    this.getList();
  }

  getList() {
    let status = "";
    let page = 0;
    switch (this.active) {
      case 0:
        status = "UNUSED";
        page = this.unusedindexPage;
        break;
      case 1:
        status = "USED";
        page = this.usedindexPage;
        break;
      case 2:
        status = "OVERDUE";
        page = this.overdueindexPage;
        break;
    }
    let data = {
      userId: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
        .userId,
      token: this.$store.getters[Vue.prototype.MutationTreeType.TOKEN_INFO]
        .token,
      status: status,
      page: page,
      pageSize: 20
    };

    Vue.prototype.$reqFormPost("/coupon/user/linklist", data, res => {
      if (res == null) {
        console.log("网络请求错误！");
        return;
      }
      if (res.data.status != 200) {
        console.log(
          "需控制错误码" + res.data.status + ",错误信息：" + res.data.message
        );
        Toast(res.data.message);
        return;
      }
      this.couponList = res.data.data.couponList;
       if (res.data.data.couponList.length == 20) {
          this.loading = false;
        }
      console.log("优惠券列表", res.data);
    });
  }
  mounted() {
    this.getList();
    console.log("优惠卷");
  }
}



